.resistor-container {
    height: 5vw;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    filter: drop-shadow(0.3vw 0.3vw #403F3F);
    border-radius: 5%;
}

.resistor-top {
    height: 1.3vw;
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* filter: drop-shadow(3px 3px #403F3F); */
    border-radius: 5%;
}

.resistor-middle {
    height: 1.3vw;
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* filter: drop-shadow(3px 3px #403F3F); */
    border-radius: 5%;
}

.resistor-bottom {
    height: 1.3vw;
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* filter: drop-shadow(3px 3px #403F3F); */
    border-radius: 5%;
}
