.breaker {
    position: relative;
    width: 5vw;
    height: 7vw;
    background-color: #a186be;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    filter: drop-shadow(3px 3px #403F3F);
    border-radius: 5%;
}
.container-breaker {
    display: flex;
    justify-content: center;
    align-items: center;
  }