.mpcb-container {
    position: relative;
    width: 8vw;
    height: 6vw;
    background-color: orange;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    filter: drop-shadow(3px 3px #403F3F);
    border-radius: 5%;
}

.mpcb-top {
    position: relative;
    top: -9px;
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.mpcb-bottom {
    position: relative;
    bottom: -9px;
    width: 100%;
    display: flex;
    justify-content: space-around;
}
