.selectorswitch-container {
    width: 13vw;
    height: 5vw;
    display: flex;
    flex-direction: column;
    filter: drop-shadow(3px 3px #403F3F);
    border-radius: 5%;
}

.selectorswitch-row-top {
    display: flex;
    justify-content: space-between;
    position: relative;
    top: -1vw;
}

.selectorswitch-row-top-space-holder {
    width: 7vw;
}

.selectorswitch-row-middle {
    color: white;
    display: flex;
    justify-content: center;
}

.selectorswitch-row-bottom {
    display: flex;
    justify-content: space-between;
    position: relative;
    bottom: -1vw;
}
