.at-container {
    position: relative;
    width: 7vw;
    height: 5vw;
    background-color: #78B83C;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    filter: drop-shadow(3px 3px #403f3f);
    border-radius: 5%;
}

.at-top {
    position: relative;
    top: -0.8vw;
    width: 100%;
    display: flex;
    justify-content: space-around;
}

.at-bottom {
    position: relative;
    bottom: -0.4vw;
    width: 100%;
    display: flex;
    justify-content: space-around;
}
